@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
*{
  font-family: 'Roboto', sans-serif;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.navbar {
    width: 100%;
    height: 50px;
    background-color: rgb(60, 63, 63);
    position: relative;
}

.navlink {
    height: 100%;
    width: 85px;
    background: #000;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px;
    position: absolute;
    right: 0;

        
}
.fa-solid {
color: #fff;
}
.shopname{
    color: #fff;
    text-decoration: none;
}
h2 {

    text-align: center;
    margin-top: 30px;
    text-transform: capitalize;
}

.cartItems {
    display: grid;
    margin: 0 auto;
    width: 450px;
    margin-top: 20px;
    grid-row-gap: 10px;
    row-gap: 10px;


}

.cartItem {

    background-color: rgb(222, 219, 219);
    height: 200px;
    border-radius: 5px;
    display: flex;
    align-items: center;

}

.cartItem img {
    width: 190px;
    height: 99%;
    padding: 5px;
    background-size: cover;
    border-radius: 5px;

}

.discription {
    margin-left: 30px;

}

.discription b {
    color: black;
}

.discription p {
    color: black;
}

.counthandel button {
    width: 20px;
    border: none;
    outline: none;
}

.counthandel input {
    width: 40px;
    border: none;
    outline: none;
    border: 1px solid lightblue;
    padding-left: 10px;

}
.shop {
    height: 100%;
    width: 100%;


}

.tittel {
    width: 100%;
    margin-top: 50px;
    height: 50px;
    display: flex;
    justify-content: center;

}

.tittel h2 {
    text-transform: uppercase;
    font-size: 500;
    font-weight: bold;
}

.products {
    display: grid;
    grid-template-columns: repeat(auto-fit, 210px);
    padding: 15px;

    margin: auto;
    grid-gap: 20px;
    gap: 20px;
}

.product {
    width: 210px;
    padding: 5px;
    border-radius: 5px;
    background-color: rgb(255, 255, 255);
    height: 300px;
}

.product img {
    width: 200px;
    height: 200px;
    background-position: center;
    background-size: contain;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
    border: 4px;
}

.product img:hover {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    cursor: pointer;

}

.discription {
    color: #5a5151;
    margin-top: 10px;
    position: relative;


}

.discription p {
    font-weight: bold;
    text-transform: capitalize;
    text-align: center;

}

.addtoCartBtn {

    padding: 5px;
    border-radius: 10px;
    border: none;
    outline: none;
    font-weight: bold;
    text-transform: capitalize;
    position: absolute;
    margin-top: 18px;
    left: 55px;
    cursor: pointer;

}
.addtoCartBtn:hover{
    background-color: black;
    color: #fff;
}


